import React, {Component} from "react";
import PropTypes from 'prop-types';
import {integer, fraction} from "../../../../../magnum2/components/Utils";

const CheckoutPriceMobile = (props) => {

        let checkoutPrice = props.checkoutPrice;
        if (!checkoutPrice) {
            return null;
        }
        return (
            <p className="u-font-small">+ {integer(checkoutPrice.gross)},{fraction(checkoutPrice.gross)} {checkoutPrice.currency} aktywacja</p>
        );

};

export default CheckoutPriceMobile;